import React, { Component } from "react";
import UserService from '../../UserService';
import FormSinUp from '../../FormSingUp/FormSingUp.jsx';
import { Link } from "react-router-dom";
import "./MyProfilePage.css"
import Logout from "../../Logout/Logout.jsx"
import Nav from "../../Nav/Nav"
import Edit from "../../../icons/icons/edit.png"
import Notifications from "../../../icons/icons/notifications.png"

export default class ProfilePage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      user: null,
    }

    this.UserService = new UserService()
  }

  componentDidMount() {

    this.UserService.getUser()
      .then(response => {
        this.setState({ ...this.state, user: response.user })
      })
  }

  printUser = () => {
    return (
      <React.Fragment>
         <Nav  title={"Mi perfil"} 
        iconleft={Notifications} 
        iconright={Edit} 
        widthR={"20px"} 
        heigthR={"20px"} 
        widthL={"17px"} 
        heigthL={"17px"}
        />
        <div className="myProfile">
          <div className="myProfileHeader">
            <img src={this.state.user.image} />
            <div className="myProfileHeaderText">
              <p className="name">{this.state.user.username}</p>
              <p className="cumple">Cumpleaos:<br></br> 22 Junio, 21 aos</p>
            </div>
          </div>
          <div className="sobreMi">
            <label>Sobre mi</label><br></br>
            <textarea placeholder="Escribe una pequea descripcin sobre ti"></textarea>
          </div>
          <div className="educacionYTrabajo">
            <label>Educacin y trabajo</label><br></br>
            <input type="text" placeholder="Aade educacin"></input><br></br>
            <input type="text" placeholder="Aade trabajo"></input>
          </div>
          <div className="myIntereses">
            <label>Intereses</label>
            <div className="myInteresesDivs">
              {this.state.user.hobbies.map(function(hobby,index){
                return(
                  <div className="myInteresesDiv">
                   {hobby}
                  </div>
                )
              })}
            </div>
          </div>
          <div className="logoutButton">
            <Logout></Logout>
          </div>
        </div>
      </React.Fragment>
    )
  }

  render() {
    return (
      <React.Fragment>
        <Nav></Nav>
        {
          this.state.user !== null &&
          <div>{this.printUser()}</div>
        }
      </React.Fragment>
    )
  }
}
